body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Playfair Display', serif;
  font-family: 'Roboto', sans-serif;
}

.container {
  max-width: 390px;
  display: flex;
  background: #fff;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 25px;
  color: green;
  text-align: left;
  font-weight: 300;
}

.quiz {
  padding: 10px;
  width: 100%;
}

.quiz__button {
  background: linear-gradient(to right, rgb(182, 244, 146), rgb(51, 139, 147));
  padding: 20px;
  border-radius: 20px;
}

.button {
  margin-top: 15px;
}


.button__item {
  display: flex;
  width: 100%;
  justify-content: center;
}

.button__back {
  margin-top: 15px;
  text-align: right;
}

.button__back button {
  cursor: pointer;
  border: 0px;
  font-size: 16px;
  font-weight: 400;
  background: transparent;
  color: #fff;
  padding: 0px;
}

.quiz__input {
  width: 100%;
}

.button__item button {
  cursor: pointer;
  background: #fff;
  border: 0px;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 16px;
  color: green;
  font-weight: 400;
  width: 100%;
}

.button__item p {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 15px;
}

.button__item input {
  border: 2px solid rgb(182, 244, 146);
  padding: 10px 20px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 15px;
}

.button__item:first-child {
  margin-bottom: 15px;
}
